import { Box, Button, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent, Typography } from "@mui/material";
import React, { useState } from "react";

const Monuments = () => {
    const [city, setCity] = useState('');
    const [monument, setMonument] = useState('');
    const [sum, setSum] = useState(0);
    const [showResult, setShowResult] = useState(false);

    const handleChangeCity = (event: SelectChangeEvent) => {
        setCity(event.target.value);
    }

    const handleChangeMonument = (event: SelectChangeEvent) => {
        setMonument(event.target.value);
        if (city === event.target.value) {
            setSum(sum + 1);
        }
    }

    const handleResult = () => {
        setShowResult(true);
    }

    const handleRefresh = () => {
        setCity('');
        setMonument('');
        setSum(0);
        setShowResult(false);
    }


    return (
        <>
            <Box display="flex"
                justifyContent="center"
                alignItems="center"
            >
                <FormControl sx={{ m: 1, minWidth: 200 }}>
                    <InputLabel id="city-label"></InputLabel>
                    <Select
                        labelId="city-label"
                        id="city"
                        value={city}
                        label=""
                        onChange={handleChangeCity}
                    >
                        <MenuItem value="rim"></MenuItem>
                        <MenuItem value="ufa"></MenuItem>
                        <MenuItem value="giza"></MenuItem>
                        <MenuItem value="ber"></MenuItem>
                        <MenuItem value="moscow"></MenuItem>
                        <MenuItem value="par"></MenuItem>
                        <MenuItem value="lon"></MenuItem>
                        <MenuItem value="sid"></MenuItem>
                    </Select>
                </FormControl>
                <FormControl sx={{ m: 1, minWidth: 200 }}>
                    <InputLabel id="monument-label"></InputLabel>
                    <Select
                        labelId="monument-label"
                        id="monument"
                        value={monument}
                        label=""
                        onChange={handleChangeMonument}
                    >
                        <MenuItem value="ufa"> </MenuItem>
                        <MenuItem value="rim"></MenuItem>
                        <MenuItem value="sid">  </MenuItem>
                        <MenuItem value="lon">-</MenuItem>
                        <MenuItem value="giza"> </MenuItem>
                        <MenuItem value="moscow"> </MenuItem>
                        <MenuItem value="ber"> </MenuItem>
                        <MenuItem value="par"></MenuItem>
                    </Select>
                </FormControl>
            </Box>
            {showResult ? <Typography variant="h5" align="center" gutterBottom>  : {sum}</Typography> : null}
            <Box
                display="flex"
                justifyContent="center"
                alignItems="center"
            >
                <Button onClick={handleResult} style={{ margin: "5px" }} variant="contained" ></Button>
                <Button onClick={handleRefresh} style={{ margin: "5px" }} variant="contained" ></Button>
            </Box>

        </>
    );
}

export default Monuments;